<template>
    <div class="white-block" v-if="Object.keys(filteredSections).length > 0">
        <div id="html-code">
            <div v-for="(section, sectionName) in filteredSections" :key="section.id">
                <h1 v-if="sectionName !== 'Правила работы'"><span style="color: rgb(0, 166, 80);">{{ sectionName }}</span></h1>
                <h1 v-else><span style="color: rgb(255, 27, 40);">{{ sectionName }}</span></h1>

                <div v-for="createdField in section.createdFields" :key="createdField.id">
                    <div v-for="field in section.fields" :key="field.name">
                        <h2 v-if="field.type === 'h2' && createdField[field.name].value.trim()">
                            {{ createdField[field.name].value }}
                        </h2>

                        <p v-else-if="field.type === 'text' || field.type === 'password'" style="white-space: pre-line;">
                            <span v-if="field.showName && createdField[field.name].value.trim()"
                                  style="font-weight: bold">{{ field.name }}:</span>
                            {{ createdField[field.name].value.trim() }}
                        </p>

                        <p v-else-if="field.type === 'link' && createdField[field.name].value.trim()">
                        <span style="color: rgb(0, 0, 238);">
                            <a v-bind:href="createdField[field.name].value"
                               target="_blank">{{ createdField[field.name].value }}</a>
                        </span>
                        </p>

                        <p v-else-if="field.type === 'comment' && createdField[field.name].value.trim()" style="white-space: pre-line;">
                            <i><b>Комментарий:</b>
                                {{ createdField[field.name].value.trim() }}</i>
                        </p>
                    </div>
                </div>
                <br>
                <hr>
            </div>
            <br>

            <details>
                <summary><h1 style="color: rgb(0, 166, 80); font-size: 24px; font-weight: bold">JSON вставка</h1></summary>
                <br>
                <a href="https://marshal327.github.io/wiki-creator/">Перейти в приложение для редактирования Wiki</a>
                <p> {{ JSON.stringify(filteredSections) }} </p>
            </details>
        </div>

        <div class="copy-btns">
            <Popper content="Скопировано!" :show="showHtmlPopper">
                <div class="copy-btn" @click="copyHtml">
                    Копировать код
                </div>
            </Popper>
            <Popper content="Скопировано!" :show="showJsonPopper">
                <div class="copy-btn" @click="copyJson">
                    Копировать JSON
                </div>
            </Popper>
        </div>
    </div>
    <div class="white-block no-sections" v-else>
        Тут пока ничего нет :(
    </div>
</template>


<script>
import Popper from "vue3-popper";

export default {
    name: "OutputCode",
    props: ["sections"],
    data() {
        return {
            showHtmlPopper: false,
            showJsonPopper: false,
        }
    },
    computed: {
        filteredSections() {
            let filteredSections = {};

            for (let key in this.sections) {
                if (this.sections[key].createdFields.length > 0) {
                    filteredSections[key] = this.sections[key]
                }
            }

            return filteredSections;
        }
    },
    methods: {
        copyText(text) {
            // eslint-disable-next-line no-useless-catch
            try {
                navigator.clipboard.writeText(text)
            } catch (e) {
                throw e;
            }
        },
        copyHtml() {
            let htmlCode = document.getElementById("html-code").outerHTML;

            this.copyText(htmlCode);

            this.showHtmlPopper = true
            setTimeout(() => this.showHtmlPopper = false, 1000);
        },
        copyJson() {
            this.copyText(JSON.stringify(this.sections));

            this.showJsonPopper = true
            setTimeout(() => this.showJsonPopper = false, 1000);
        }
    },
    components: {
        Popper,
    },
}
</script>

<style scoped>
.no-sections {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 38px;
    color: var(--dark-grey);
}

.copy-btns {
    display: flex;
    position: absolute;
    top: 20px;
    right: 20px;
    gap: 20px;
}

.copy-btn {
    background-color: var(--green-color);
    border-radius: 5px;
    padding: 5px 10px;
    color: #ffffff;
    transition: .3s;
}

.copy-btn:hover {
    transition: .3s;
    filter: var(--light-shadow);
    cursor: pointer;
}

#html-code {
    height: 100%;
    overflow: auto;
}
</style>