            </div>

            <textarea class="modal__text" v-model="modalText"></textarea>
            <div class="modal__btn btn-dotted btn-dotted_default" @click="this.$emit('submit-modal', modalText)">
                Подтвердить
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            modalText: ""
        }
    },
    methods: {
        closeModal() {
            this.$emit('close-modal')
        }
    },
    mounted() {
        document.addEventListener("click", (e) => {
            if (e.target === this.$refs.modal_wrapper) {
                this.closeModal()
            }
        })
    },
    name: "TextareaModal",
    emits: ["submit-modal", "close-modal"],
}
</script>

<style scoped>
.modal {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 6;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgba(0, 0, 0, 0.4);
    backdrop-filter: blur(3px);
}

.modal__body {
    width: 700px;
}

.modal__text {
    width: 100%;
    box-sizing: border-box;
    border: 0;
    border-radius: 10px;
    height: 400px;
    resize: none;
    padding: 10px;
}

.modal__btn {
    margin-top: 10px;
    border-radius: 10px;
    border: 0;
    padding: 20px 0;
}

.modal__btn:hover {
    border: 0;
}

.modal__close {
    float: right;
    margin-right: -45px;
}
