<template>
    <div class="btn-dotted btn-dotted_default" @click="showModal = true">
        Вставить как текст
    </div>
    <transition name="scale">
        <TextareaModal
            v-if="showModal"
            @submit-modal="insertSectionText"
            @close-modal="showModal = false"
        />
    </transition>
</template>

<script>
import TextareaModal from "@/components/TextareaModal";

export default {
    name: "InsertSectionText",
    data() {
        return {
            showModal: false
        }
    },
    methods: {
        insertSectionText(modalText) {
            try {
                let json = JSON.parse(modalText)
                this.showModal = false

                this.$emit("insert-section-text", json)
            } catch (e) {
                alert("Произошла ошибка!")
            }
        }
    },
    emits: ["insert-section-text"],
    components: {TextareaModal}
}
</script>

<style scoped>
.scale-enter-active {
    animation: scale .5s;
}
.scale-leave-active {
    animation: scale .5s reverse;
}
@keyframes scale {
    0% {
        transform: scale(1.2);
        opacity: 0;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}
</style>